*{margin:0;padding:0;box-sizing:border-box;text-transform:capitalize}body{margin:0;font-family:"Poppins",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;max-width:calc(100vw - (100vw - 100%));overflow-x:hidden;background-color:#101c31;color:#fff}body a{text-decoration:none;line-height:100%;color:#fff}body li{list-style:none}body h1,body h2,body h3,body h4,body h5{line-height:100%;font-weight:400;letter-spacing:1px}body p,body a,body span,body strong{line-height:100%;font-size:16px;letter-spacing:0.8px}body .heading{font-size:70px;font-family:'Roboto', sans-serif;letter-spacing:6px;font-weight:900}@media (max-width: 550px){body .heading{font-size:35px;letter-spacing:2px}}body .h1{font-size:35px;letter-spacing:1.5px}@media (max-width: 550px){body .h1{font-size:30px}}body .h2{font-size:30px;letter-spacing:1.3px}@media (max-width: 550px){body .h2{font-size:25px}}body .h3{font-size:25px;letter-spacing:1.2px}@media (max-width: 550px){body .h3{font-size:22px}}body .h4{font-size:22px;letter-spacing:1.1px}@media (max-width: 550px){body .h4{font-size:20px}}body .h5{font-size:20px;letter-spacing:1px}@media (max-width: 550px){body .h5{font-size:18px}}body .title{font-size:30px;letter-spacing:1.3px}@media (max-width: 550px){body .title{font-size:25px}}body .sub-title{font-size:22px;letter-spacing:1.3px}@media (max-width: 550px){body .sub-title{font-size:20px}}body .section-container{padding:80px 100px}@media (max-width: 768px){body .section-container{padding:50px 80px}}@media (max-width: 550px){body .section-container{padding:20px 20px}}body .btn-btn{text-decoration:none;cursor:pointer;border:1px solid #75efff;border-radius:8px;height:40px;width:120px;padding:0;outline:none;overflow:hidden;color:#75efff;transition:color 0.3s 0.1s ease-out;display:flex;align-items:center;justify-content:center;position:relative;background-color:transparent}body .btn-btn::before{position:absolute;top:0;left:0;right:0;bottom:0;margin:auto;content:'';border-radius:50%;display:block;width:240px;height:240px;line-height:240px;left:-60px;text-align:center;transition:box-shadow 0.5s ease-out;z-index:-1}body .btn-btn:hover{color:#fff}body .btn-btn:hover::before{box-shadow:inset 0 0 0 120px rgba(117,239,255,0.3)}

nav{-webkit-backdrop-filter:blur(3px);backdrop-filter:blur(3px);overflow:hidden}nav .nav-container{padding:10px 40px;display:flex;align-items:center;justify-content:space-between}nav .nav-container .logo-container a img{max-width:70px}@media (max-width: 550px){nav .nav-container .logo-container a img{max-width:50px}}@media (max-width: 768px){nav .nav-container .link-container{display:none}}nav .nav-container .link-container ul{display:flex;align-items:center;justify-content:space-between}nav .nav-container .link-container ul li{margin-left:20px}nav .nav-container .link-container ul li a{font-family:"Roboto Mono",monospace;position:relative;transition:all 0.3s ease-out}nav .nav-container .link-container ul li a::after{position:absolute;content:'';width:100%;height:2px;background-color:#75efff;left:0;bottom:-5px;transition:all 0.3s ease-out;-webkit-transform:scale(0);transform:scale(0)}nav .nav-container .link-container ul li a.active{color:#75efff}nav .nav-container .link-container ul li a.active::after{-webkit-transform:scaleY(1);transform:scaleY(1)}nav .nav-container .link-container ul li a:hover{color:#75efff}nav .nav-container .link-container ul li a:hover::after{-webkit-transform:scale(1);transform:scale(1)}nav .nav-container .link-container ul li button{font-family:"Roboto Mono",monospace}nav .nav-container .menu-container{display:none;width:25px}@media screen and (max-width: 768px){nav .nav-container .menu-container{display:block}}nav .nav-container .menu-container .menus{display:flex;flex-direction:column}nav .nav-container .menu-container .menus .menu-bar{background:#fff;width:30px;height:3px;margin:3px 0;transition:all 0.3s ease-in-out}nav .nav-container .menu-container .menus.active .menu-bar:first-child{-webkit-transform:rotate(-45deg) translate(-9px, 4px);transform:rotate(-45deg) translate(-9px, 4px)}nav .nav-container .menu-container .menus.active .menu-bar:nth-child(2){opacity:0}nav .nav-container .menu-container .menus.active .menu-bar:last-child{-webkit-transform:rotate(45deg) translate(-8px, -5px);transform:rotate(45deg) translate(-8px, -5px)}

.home{min-height:100vh;position:relative}.home .header-bg-one{position:fixed;top:-300px;left:-5%;width:110vw;height:110vw;min-width:100vh;min-height:100vh;background:radial-gradient(rgba(6,5,46,0.8) 0%, rgba(6,5,46,0.8) 8.3333%, rgba(6,5,46,0.5) 8.3333%, rgba(6,5,46,0.5) 16.6666%, rgba(6,5,46,0.8) 16.6666%, rgba(6,5,46,0.8) 25%, rgba(6,5,46,0.5) 25%, rgba(6,5,46,0.5) 33.333%, rgba(6,5,46,0.8) 33.3333%, rgba(6,5,46,0.8) 41.666%, rgba(6,5,46,0.5) 41.666%, rgba(6,5,46,0.5) 50%, rgba(6,5,46,0.8) 50%, rgba(6,5,46,0.8) 58.333%, rgba(6,5,46,0.4) 58.333%, rgba(6,5,46,0.4) 66.666%, rgba(6,5,46,0.6) 66.666%, rgba(6,5,46,0.6) 75%, rgba(6,5,46,0.4) 75%, rgba(6,5,46,0.3) 83.333%, rgba(6,5,46,0.1) 83.333%, rgba(6,5,46,0.1) 91.666%, rgba(6,5,46,0.1) 91.666%, rgba(6,5,46,0.1) 100%);background-size:cover;z-index:-10;opacity:0.5;will-change:transform}@media (max-width: 900px){.home .header-bg-one{top:-200px}}@media (max-width: 768px){.home .header-bg-one{top:0}}.home .bg-fixed{position:fixed;width:100px;height:100px;background-color:rgba(255,255,255,0.05);border-radius:50%;z-index:-10;will-change:transform}.home .header-bg-two{width:50px;height:50px;left:50%;top:50%}.home .header-bg-three{width:60px;height:60px;left:30%;top:20%}.home .header-bg-three{width:20px;height:20px;left:70%;top:90%}.home .header-bg-four{width:70px;height:70px;left:80%;top:40%}.home .header-bg-five{width:40px;height:40px;left:20%;top:70%}.home .header-bg-six{width:80px;height:80px;left:40%;top:30%}.home .header-bg-seven{width:40px;height:40px;left:60%;top:90%}.home .header-bg-eight{width:30px;height:30px;left:50%;top:20%}

